"use client";

import React, { useState, useEffect, useRef, useCallback } from "react";
import { motion, AnimatePresence } from "framer-motion";
import {
  Play,
  Pause,
  RotateCcw,
  Coffee,
  Brain,
  Armchair,
  Timer,
  History,
  Maximize2,
  Minimize2,
  CheckCircle2,
  Square,
  Settings2,
  X,
} from "lucide-react";
import { usePomodoro } from "@/lib/db/usePomodoro";
import { format } from "date-fns";

// --- TYPES & CONFIG ---
type Mode = "work" | "short" | "long";

interface TimerSettings {
  work: number;
  short: number;
  long: number;
}

const MODE_CONFIG = {
  work: {
    label: "Deep Focus",
    color: "text-indigo-600",
    bg: "bg-indigo-500",
    gradient: "from-indigo-500 to-purple-600",
    icon: Brain,
  },
  short: {
    label: "Short Break",
    color: "text-teal-600",
    bg: "bg-teal-500",
    gradient: "from-teal-400 to-emerald-500",
    icon: Coffee,
  },
  long: {
    label: "Long Break",
    color: "text-blue-600",
    bg: "bg-blue-500",
    gradient: "from-blue-400 to-cyan-500",
    icon: Armchair,
  },
};

export default function PomodoroPage() {
  const { addSession, todaySessions } = usePomodoro();

  // --- STATE ---
  const [mode, setMode] = useState<Mode>("work");
  const [settings, setSettings] = useState<TimerSettings>({
    work: 25,
    short: 5,
    long: 15,
  });

  const [timeLeft, setTimeLeft] = useState(settings.work * 60);
  const [isActive, setIsActive] = useState(false);
  const [isZenMode, setIsZenMode] = useState(false);
  const [isSettingsOpen, setIsSettingsOpen] = useState(false);

  // Refs
  const audioRef = useRef<HTMLAudioElement | null>(null);
  const timerInterval = useRef<NodeJS.Timeout | null>(null);
  const initialTimeRef = useRef(settings.work * 60);

  // --- HELPER: Switch Mode ---
  const switchMode = useCallback(
    (newMode: Mode) => {
      if (isActive) return;
      setMode(newMode);
      setTimeLeft(settings[newMode] * 60);
      initialTimeRef.current = settings[newMode] * 60;

      localStorage.setItem("pomodoro_mode", newMode);
      localStorage.removeItem("pomodoro_target_time");
    },
    [isActive, settings]
  );

  // --- HELPER: Timer Complete ---
  const handleTimerComplete = useCallback(() => {
    setIsActive(false);
    setTimeLeft(0);
    localStorage.removeItem("pomodoro_target_time");
    audioRef.current?.play().catch(() => {});

    const duration = settings[mode];
    addSession(duration, mode === "work" ? "work" : "break");

    setTimeout(() => {
      if (mode === "work") {
        if (confirm("Focus complete! Start break?")) switchMode("short");
      } else {
        if (confirm("Break over! Start focus?")) switchMode("work");
      }
    }, 100);
  }, [mode, settings, addSession, switchMode]);

  // --- 1. INITIALIZATION ---
  useEffect(() => {
    audioRef.current = new Audio(
      "https://actions.google.com/sounds/v1/alarms/beep_short.ogg"
    );

    const savedSettings = localStorage.getItem("pomodoro_settings");
    let currentSettings = settings;

    if (savedSettings) {
      const parsed = JSON.parse(savedSettings);
      setSettings(parsed);
      currentSettings = parsed;
    }

    const savedTarget = localStorage.getItem("pomodoro_target_time");
    const savedMode = localStorage.getItem("pomodoro_mode") as Mode;

    if (savedTarget && savedMode) {
      const targetTime = parseInt(savedTarget, 10);
      const now = Date.now();
      const diff = Math.ceil((targetTime - now) / 1000);

      if (diff > 0) {
        setMode(savedMode);
        setTimeLeft(diff);
        setIsActive(true);
        initialTimeRef.current = currentSettings[savedMode] * 60;
      } else {
        localStorage.removeItem("pomodoro_target_time");
        localStorage.removeItem("pomodoro_mode");
        setMode(savedMode);
        setTimeLeft(0);
      }
    } else {
      setTimeLeft(currentSettings.work * 60);
      initialTimeRef.current = currentSettings.work * 60;
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // --- 2. TIMER LOOP ---
  useEffect(() => {
    if (isActive) {
      if (!localStorage.getItem("pomodoro_target_time")) {
        const targetTime = Date.now() + timeLeft * 1000;
        localStorage.setItem("pomodoro_target_time", targetTime.toString());
        localStorage.setItem("pomodoro_mode", mode);
      }

      timerInterval.current = setInterval(() => {
        const targetTime = parseInt(
          localStorage.getItem("pomodoro_target_time") || "0",
          10
        );
        const now = Date.now();
        const diff = Math.ceil((targetTime - now) / 1000);

        if (diff <= 0) {
          handleTimerComplete();
        } else {
          setTimeLeft(diff);
          document.title = `${Math.floor(diff / 60)}:${(diff % 60)
            .toString()
            .padStart(2, "0")} - ${MODE_CONFIG[mode].label}`;
        }
      }, 1000);
    } else {
      if (timerInterval.current) clearInterval(timerInterval.current);
      localStorage.removeItem("pomodoro_target_time");
      document.title = "TaskGlyph - Focus";
    }
    return () => {
      if (timerInterval.current) clearInterval(timerInterval.current);
    };
  }, [isActive, mode, timeLeft, handleTimerComplete]);

  // --- HANDLERS ---

  const handleStopAndSave = () => {
    if (!isActive && timeLeft === initialTimeRef.current) return;
    setIsActive(false);
    localStorage.removeItem("pomodoro_target_time");

    const timeSpentSeconds = initialTimeRef.current - timeLeft;
    const timeSpentMinutes = Math.floor(timeSpentSeconds / 60);

    if (timeSpentMinutes >= 1) {
      if (confirm(`Save ${timeSpentMinutes} minutes session?`)) {
        addSession(timeSpentMinutes, mode === "work" ? "work" : "break");
      }
    }
    setTimeLeft(settings[mode] * 60);
  };

  const toggleTimer = () => {
    if (!isActive && timeLeft === 0) setTimeLeft(settings[mode] * 60);
    setIsActive(!isActive);
  };

  const resetTimer = () => {
    setIsActive(false);
    localStorage.removeItem("pomodoro_target_time");
    setTimeLeft(settings[mode] * 60);
  };

  const saveSettings = (newSettings: TimerSettings) => {
    setSettings(newSettings);
    localStorage.setItem("pomodoro_settings", JSON.stringify(newSettings));
    if (!isActive) {
      setTimeLeft(newSettings[mode] * 60);
      initialTimeRef.current = newSettings[mode] * 60;
    }
    setIsSettingsOpen(false);
  };

  const formatTime = (seconds: number) => {
    const m = Math.floor(seconds / 60);
    const s = seconds % 60;
    return `${m}:${s < 10 ? "0" : ""}${s}`;
  };

  // --- VISUALS ---
  const radius = 120;
  const circumference = 2 * Math.PI * radius;
  const totalTime = settings[mode] * 60;
  const progress =
    totalTime > 0 ? ((totalTime - timeLeft) / totalTime) * circumference : 0;

  const CurrentIcon = MODE_CONFIG[mode].icon;
  const sessionsCount =
    todaySessions?.filter((s) => s.type === "work").length || 0;
  const totalFocusMinutes =
    todaySessions
      ?.filter((s) => s.type === "work")
      .reduce((acc, curr) => acc + curr.durationMinutes, 0) || 0;

  return (
    <div
      className={`fixed inset-0 w-screen h-screen overflow-hidden transition-colors duration-700 flex items-center justify-center ${
        isZenMode ? "bg-[#0F1115]" : "bg-[#F3F4F6]"
      }`}
    >
      {/* Ambient Background */}
      {!isZenMode && (
        <div className="absolute inset-0 overflow-hidden pointer-events-none">
          <div
            className={`absolute top-[-10%] left-[-10%] w-[600px] h-[600px] rounded-full mix-blend-multiply filter blur-[120px] opacity-30 animate-blob ${MODE_CONFIG[mode].bg}`}
          ></div>
          <div
            className={`absolute bottom-[-10%] right-[-10%] w-[600px] h-[600px] rounded-full mix-blend-multiply filter blur-[120px] opacity-30 animate-blob animation-delay-2000 ${MODE_CONFIG[mode].bg}`}
          ></div>
        </div>
      )}

      <div
        className={`w-full max-w-6xl grid grid-cols-1 lg:grid-cols-3 gap-6 p-6 z-10 h-full max-h-[650px] transition-all duration-500 ${
          isZenMode ? "place-items-center" : ""
        }`}
      >
        {/* --- LEFT: TIMER CARD --- */}
        <motion.div
          layout
          className={`
                relative flex flex-col items-center justify-between
                bg-white/80 backdrop-blur-2xl rounded-[40px] shadow-2xl border border-white/60 p-8
                transition-all duration-700 ease-in-out
                ${
                  isZenMode
                    ? "fixed inset-0 w-screen h-screen rounded-none border-none bg-black/90 z-50 justify-center"
                    : "lg:col-span-2 w-full h-full"
                }
            `}
        >
          {/* Top Bar */}
          <div
            className={`flex justify-between w-full ${
              isZenMode ? "absolute top-8 px-10" : ""
            }`}
          >
            {/* Mode Switcher */}
            <div
              className={`flex bg-gray-100/80 p-1 rounded-2xl transition-colors ${
                isZenMode ? "bg-white/10" : ""
              }`}
            >
              {(
                Object.keys(MODE_CONFIG) as Array<keyof typeof MODE_CONFIG>
              ).map((m) => (
                <button
                  key={m}
                  onClick={() => switchMode(m)}
                  disabled={isActive}
                  className={`flex items-center gap-2 px-4 py-2 rounded-xl text-sm font-bold transition-all duration-300 ${
                    mode === m
                      ? "bg-white shadow-sm text-black"
                      : `text-gray-400 hover:text-gray-600 ${
                          isZenMode ? "text-white/40 hover:text-white" : ""
                        } ${isActive ? "opacity-50 cursor-not-allowed" : ""}`
                  }`}
                >
                  <span className={`${mode === m ? MODE_CONFIG[m].color : ""}`}>
                    {MODE_CONFIG[m].label}
                  </span>
                </button>
              ))}
            </div>

            {/* Actions */}
            <div className="flex gap-2">
              <button
                onClick={() => setIsSettingsOpen(true)}
                className={`p-3 rounded-full transition-colors ${
                  isZenMode
                    ? "bg-white/10 hover:bg-white/20 text-white"
                    : "bg-gray-100 hover:bg-gray-200 text-gray-500"
                }`}
              >
                <Settings2 size={20} />
              </button>
              <button
                onClick={() => setIsZenMode(!isZenMode)}
                className={`p-3 rounded-full transition-colors ${
                  isZenMode
                    ? "bg-white/10 hover:bg-white/20 text-white"
                    : "bg-gray-100 hover:bg-gray-200 text-gray-500"
                }`}
              >
                {isZenMode ? <Minimize2 size={20} /> : <Maximize2 size={20} />}
              </button>
            </div>
          </div>

          {/* [FIX #2] TIMER CIRCLE (Increased Size) */}
          <div className="relative flex items-center justify-center flex-1">
            <div
              className={`absolute inset-0 rounded-full blur-[60px] opacity-30 bg-gradient-to-tr ${
                MODE_CONFIG[mode].gradient
              } transition-opacity duration-700 ${
                isActive ? "opacity-60" : ""
              }`}
            ></div>

            {/* Changed w-80 to w-96 for bigger circle */}
            <div className="relative w-96 h-96">
              <svg className="w-full h-full transform -rotate-90 drop-shadow-xl">
                <circle
                  cx="192" // Center is half of 384 (w-96)
                  cy="192"
                  r={radius}
                  stroke="currentColor"
                  strokeWidth="6"
                  fill="transparent"
                  className={isZenMode ? "text-white/5" : "text-gray-100"}
                />
                <circle
                  cx="192"
                  cy="192"
                  r={radius}
                  stroke="url(#gradient)"
                  strokeWidth="6"
                  fill="transparent"
                  strokeDasharray={circumference}
                  strokeDashoffset={-progress}
                  strokeLinecap="round"
                  className="transition-all duration-1000 ease-linear"
                />
                <defs>
                  <linearGradient
                    id="gradient"
                    x1="0%"
                    y1="0%"
                    x2="100%"
                    y2="0%"
                  >
                    <stop
                      offset="0%"
                      stopColor={
                        mode === "work"
                          ? "#6366f1"
                          : mode === "short"
                          ? "#14b8a6"
                          : "#3b82f6"
                      }
                    />
                    <stop
                      offset="100%"
                      stopColor={
                        mode === "work"
                          ? "#a855f7"
                          : mode === "short"
                          ? "#10b981"
                          : "#06b6d4"
                      }
                    />
                  </linearGradient>
                </defs>
              </svg>

              {/* [FIX #2] Text Centering & Sizing */}
              <div className="absolute inset-0 flex flex-col items-center justify-center">
                <span
                  className={`text-7xl font-bold tracking-tighter font-mono tabular-nums leading-none ${
                    isZenMode ? "text-white" : "text-gray-900"
                  }`}
                >
                  {formatTime(timeLeft)}
                </span>
                <div
                  className={`flex items-center gap-2 mt-6 px-4 py-1.5 rounded-full bg-opacity-10 ${
                    isZenMode
                      ? "bg-white/10 text-white/60"
                      : "bg-gray-100 text-gray-500"
                  }`}
                >
                  <CurrentIcon size={14} />
                  <span className="text-xs font-bold uppercase tracking-widest">
                    {isActive ? "Focusing" : "Paused"}
                  </span>
                </div>
              </div>
            </div>
          </div>

          {/* [FIX #1] CONTROLS - Bigger Buttons & Effects */}
          <div className="flex flex-col items-center gap-4 mb-4 w-full">
            <div className="flex items-center justify-center gap-12">
              {/* Reset Button */}
              <button
                onClick={resetTimer}
                className={`w-16 h-16 rounded-2xl flex items-center justify-center transition-all hover:scale-110 active:scale-95 duration-300 shadow-sm hover:shadow-md ${
                  isZenMode
                    ? "bg-white/10 text-white hover:bg-white/20"
                    : "bg-gray-100 text-gray-500 hover:bg-gray-200 hover:text-gray-800"
                }`}
                title="Reset"
              >
                <RotateCcw size={24} />
              </button>

              {/* Play/Pause Button (Center - Largest) */}
              <button
                onClick={toggleTimer}
                className={`w-24 h-24 rounded-[2.5rem] flex items-center justify-center text-white shadow-xl hover:shadow-2xl hover:scale-105 active:scale-95 transition-all bg-gradient-to-tr ${MODE_CONFIG[mode].gradient}`}
              >
                {isActive ? (
                  <Pause size={36} fill="currentColor" />
                ) : (
                  <Play size={36} fill="currentColor" className="ml-1" />
                )}
              </button>

              {/* Stop & Save Button */}
              <div className="w-16 h-16 flex items-center justify-center">
                {timeLeft !== settings[mode] * 60 ? (
                  <button
                    onClick={handleStopAndSave}
                    className={`group relative w-16 h-16 rounded-2xl flex items-center justify-center transition-all hover:scale-110 active:scale-95 duration-300 shadow-sm hover:shadow-md ${
                      isZenMode
                        ? "bg-red-500/20 text-red-400 hover:bg-red-500 hover:text-white"
                        : "bg-red-50 text-red-500 hover:bg-red-100 hover:text-red-600"
                    }`}
                    title="Finish Early"
                  >
                    <Square size={24} fill="currentColor" />
                  </button>
                ) : (
                  // Invisible placeholder to keep layout balanced
                  <div className="w-16 h-16" />
                )}
              </div>
            </div>
          </div>
        </motion.div>

        {/* --- RIGHT: STATS (Hidden in Zen) --- */}
        {!isZenMode && (
          <div className="flex flex-col gap-6 h-full">
            {/* Daily Goal */}
            <div className="bg-white/80 backdrop-blur-xl rounded-[40px] shadow-lg border border-white/50 p-8 flex-shrink-0">
              <h3 className="text-lg font-bold text-gray-800 mb-6 flex items-center gap-2">
                <Timer size={20} className="text-indigo-500" /> Today&apos;s
                Progress
              </h3>
              <div className="space-y-5">
                <div className="bg-indigo-50/50 p-5 rounded-3xl border border-indigo-100">
                  <div className="flex justify-between items-end mb-2">
                    <span className="text-4xl font-bold text-indigo-600">
                      {sessionsCount}
                    </span>
                    <span className="text-xs font-bold text-indigo-400 uppercase mb-1">
                      Sessions
                    </span>
                  </div>
                  <div className="w-full h-1.5 bg-indigo-100 rounded-full overflow-hidden">
                    <div
                      className="h-full bg-indigo-500 rounded-full"
                      style={{ width: `${Math.min(sessionsCount * 10, 100)}%` }}
                    ></div>
                  </div>
                </div>

                <div className="bg-teal-50/50 p-5 rounded-3xl border border-teal-100">
                  <div className="flex justify-between items-end mb-2">
                    <span className="text-4xl font-bold text-teal-600">
                      {(totalFocusMinutes / 60).toFixed(1)}
                    </span>
                    <span className="text-xs font-bold text-teal-400 uppercase mb-1">
                      Hours
                    </span>
                  </div>
                  <div className="w-full h-1.5 bg-teal-100 rounded-full overflow-hidden">
                    <div
                      className="h-full bg-teal-500 rounded-full"
                      style={{
                        width: `${Math.min(
                          (totalFocusMinutes / 60) * 20,
                          100
                        )}%`,
                      }}
                    ></div>
                  </div>
                </div>
              </div>
            </div>

            {/* History with Invisible Scrollbar */}
            <div className="flex-1 bg-white/80 backdrop-blur-xl rounded-[40px] shadow-lg border border-white/50 p-8 flex flex-col overflow-hidden">
              <h3 className="text-lg font-bold text-gray-800 mb-4 flex items-center gap-2">
                <History size={20} className="text-gray-400" /> Activity
              </h3>

              <div className="flex-1 overflow-y-auto space-y-3 [&::-webkit-scrollbar]:hidden [-ms-overflow-style:none] [scrollbar-width:none]">
                {todaySessions && todaySessions.length > 0 ? (
                  [...todaySessions].reverse().map((session) => (
                    <div
                      key={session.id}
                      className="flex items-center justify-between p-4 bg-white rounded-2xl border border-gray-50 shadow-sm hover:shadow-md transition-all"
                    >
                      <div className="flex items-center gap-4">
                        <div
                          className={`p-2.5 rounded-full ${
                            session.type === "work"
                              ? "bg-indigo-100 text-indigo-600"
                              : "bg-teal-100 text-teal-600"
                          }`}
                        >
                          {session.type === "work" ? (
                            <CheckCircle2 size={18} />
                          ) : (
                            <Coffee size={18} />
                          )}
                        </div>
                        <div>
                          <p className="text-sm font-bold text-gray-700">
                            {session.type === "work" ? "Deep Focus" : "Break"}
                          </p>
                          <p className="text-[10px] text-gray-400 font-medium">
                            {format(new Date(session.completedAt), "h:mm a")}
                          </p>
                        </div>
                      </div>
                      <span className="text-xs font-bold text-gray-500 bg-gray-50 px-2 py-1 rounded-md">
                        {session.durationMinutes}m
                      </span>
                    </div>
                  ))
                ) : (
                  <div className="h-full flex flex-col items-center justify-center text-gray-300 text-sm">
                    <p>No sessions yet.</p>
                  </div>
                )}
              </div>
            </div>
          </div>
        )}
      </div>

      {/* SETTINGS MODAL */}
      <AnimatePresence>
        {isSettingsOpen && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 backdrop-blur-sm"
          >
            <motion.div
              initial={{ scale: 0.9, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.9, opacity: 0 }}
              className="bg-white rounded-[40px] shadow-2xl w-96 p-8"
            >
              <div className="flex justify-between items-center mb-8">
                <h2 className="text-xl font-bold text-gray-800">
                  Timer Settings
                </h2>
                <button
                  onClick={() => setIsSettingsOpen(false)}
                  className="p-2 hover:bg-gray-100 rounded-full transition-colors"
                >
                  <X size={20} className="text-gray-500" />
                </button>
              </div>
              <div className="space-y-6">
                {(["work", "short", "long"] as const).map((settingMode) => (
                  <div key={settingMode}>
                    <label className="block text-xs font-bold text-gray-400 uppercase mb-2">
                      {MODE_CONFIG[settingMode].label} (min)
                    </label>
                    <input
                      type="number"
                      value={settings[settingMode]}
                      onChange={(e) =>
                        setSettings({
                          ...settings,
                          [settingMode]: Number(e.target.value),
                        })
                      }
                      className="w-full bg-gray-50 border-none rounded-2xl px-5 py-3.5 text-gray-800 font-bold focus:outline-none focus:ring-2 focus:ring-indigo-100"
                    />
                  </div>
                ))}
              </div>
              <div className="mt-8">
                <button
                  onClick={() => saveSettings(settings)}
                  className="w-full bg-black text-white py-4 rounded-2xl font-bold hover:bg-gray-900 transition-all shadow-lg hover:shadow-xl"
                >
                  Save Changes
                </button>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}
